<template>
  <div>
    <!-- header begin -->
    <div class="nav-bar">
      <div class="nav-bar-left" @click="goWdxmBj()">
        <i class="iconfont iconicon-back" />
      </div>
      <div class="nav-bar-title">报价历史</div>
      <div class="nav-bar-right">
        <div class="right-option blue" @click="showXzlcPopup()">
          选择轮次  <i class="iconfont iconarrow-down" />
        </div>
      </div>
    </div>

    <div v-if="pageStyles == 0" class="container">

      <div
        v-for="(dataItem, index) in FBNRList"
        :key="index"
        class="common-info"
      >
        <div class="title title-page">
          <div class="title-left">
            <h2>{{ dataItem.title }}</h2>
          </div>
        </div>
        <div
          v-for="(items, indexs) in dataItem.data"
          :key="indexs"
          class="common-main box"
        >
          <div
            v-for="(dxItems, dxIndexs) in items"
            :key="dxIndexs"
            :class="dxItems.zdlxxh? 'page-list ':'page-list hide'"
          >
            <!-- sjlx 数据类型 1：数字型 3：字符型 12：图片型 15：附件型 20:pdf-->
            <!-- srfs 输入方式 1-输入 2-代码选择 -公式计算 -->
            <!-- bz 标志 0-输入/文字 1-输入 -->
            <!-- sfbt 是否必填  1-必填 2-非必填-->

            <!-- sfkyxg 是否可以输入-->

            <div class="pagelist-left">{{ dxItems.name }}</div>
            <div v-if="dxItems.sjlx===12|| dxItems.sjlx===15 || dxItems.sjlx===20">
              <div
                v-for="(flItems, flIndexs) in dxItems.showvalues"
                :key="flIndexs"
                :class="dxItems.sjlx===12|| dxItems.sjlx===15 || dxItems.sjlx===20? 'pagelist-right blue':'pagelist-right'"
                @click="loadfile(dxItems.files[flIndexs],dxItems.showvalues[flIndexs])"
              >
                {{ flItems }}

              </div>
            </div>
            <div v-else :class="dxItems.sjlx===12|| dxItems.sjlx===15 || dxItems.sjlx===20? 'pagelist-right blue':'pagelist-right'">{{ dxItems.showvalue }}
            </div>

          </div>
        </div>
        <!-- 多表结束 -->

      </div>

    </div>
    <div v-else>
      <error-page
        :page-style="pageStyles"
        @errorButton="parentReceive"
      />
    </div>
    <!-- 分包列表弹窗 begin -->
    <van-popup v-model="showFb" round closeable position="bottom" :style="{ height: '60%' }">
      <div class="common-popup">
        <div class="popup-title">分包列表</div>
        <!-- <div class="popup-content">
          <div
            class="package-list"
          >
            <ul>
              <li
                v-for="(item, index) in FbList"
                :key="index"
                :class="getFBZT(item.tbzt)"
                @click="cheackFbTab(item.id)"
              ><span class="tit">{{ item.bmc }}</span></li>
            </ul>
          </div>
        </div> -->
        <!-- 说明文字 begin -->
        <div class="progressblock-tips">
          <ul>
            <li class="done">已完成</li>
            <li class="active">当前分包</li>
            <li>未进行</li>
          </ul>
        </div>
        <!-- 说明文字 end -->
      </div>
    </van-popup>
    <!-- 分包列表弹窗 end -->
    <!-- 选择轮次弹窗 begin -->
    <van-popup
      v-model="XzlcShow"
      position="bottom"
    >
      <van-picker
        show-toolbar
        title="选择轮次"
        :columns="realArray"
        @confirm="onConfirm"
        @cancel="onCancel"
      />
    </van-popup>
    <!-- 选择轮次弹窗 end -->
  </div>
</template>

<script>
import { mapState } from 'vuex'
import errorPage from '@/components/result-page/index'
export default {

  name: 'WdxmBjLs',
  components: {
    errorPage
  },
  data() {
    return {
      pageStyles: 0, // 页面状态展示
      XzlcShow: false,
      showFb: false,
      FbList: {}, // 分包list
      activFb: 0, // 当前选中得分包
      FBNRList: {}, // 分包内容
      dqlcid: '', // 当前轮次
      realArray: [],
      dmxzArray: ''
    }
  },
  computed: {
    // 取数据
    ...mapState({
      isLogin: state => state.mh.mhisLogin,
      userInfo: state => state.mh.mhUserInfo,
      xmid: state => state.gyswdxm.xmid
    })
  },
  created() {
    // this.getFBList()
    this.getBjlcMap(this.$route.params.fbid)
  },
  methods: {
    loadfile(url, name) {
      console.info('url------', url)
      if (process.env.NODE_ENV !== 'development') {
        try {
          window.myapp.downloadFile(url)
        } catch (err) {
          console.log(err)
        }
        this.$bridge.callhandler('downloadFile', String(url), (data) => {
        })
      } else {
        this.$toast('开发环境不支持')
      }
    },
    showPopup() {
      this.showFb = true
    },
    showXzlcPopup() {
      this.XzlcShow = true
    },
    getFBZT(fbzt) {
      switch (fbzt) {
        case 2:
          return 'done'
        case 1:
          return ''
        case 0:
          return 'active'
        default:
          break
      }
    },
    // 获取tab分包状态
    getFbTab(fbzt) {
      switch (fbzt) {
        case 2:
          return ' tab_item done'
        case 1:
          return 'tab_item'
        case 0:
          return 'tab_item active'
        default:
          break
      }
    },
    // 切换分包
    // cheackFbTab(index) {
    //   this.activFb = index
    //   for (let i = 0; i < this.FbList.length; i++) {
    //     if (this.FbList[i].tbzt === 0) {
    //       this.FbList[i].tbzt = 1
    //     }
    //   }
    //   this.FbList[index].tbzt = 0
    //   this.getFBNRList(this.FbList[index].id)
    // },
    goWdxmBj() {
      this.$router.push({
        name: 'Wdxmxmxq'
      })
    },
    findKey(obj, value, compare = (a, b) => a === b) {
      return Object.keys(obj).find(k => compare(obj[k], value))
    },
    // 再次点击
    parentReceive() {
      this.getBjlcMap(this.$route.params.fbid)
    },
    // 获取报价轮次
    getBjlcMap(fbid) {
      this.$store
        .dispatch('gyswdxm/getBjlcMapAction', { 'xmid': this.xmid, 'zzid': this.userInfo.zzid, 'fbid': fbid })
        .then(res => {
          console.info('res======', res.length)
          if (JSON.stringify(res) !== '{}') {
            const array = []
            for (const keys in res) {
              array.push(keys)
              this.realArray.push(res[keys])
            }
            this.dmxzArray = res
            this.dqlcid = array[0]
            this.getFBNRList(this.$route.params.fbid)
          } else {
            this.pageStyles = 1
            // this.$toast.fail('暂无报价历史')
          }
        })
        .catch((error) => {
          this.pageStyles = 3
          console.info('error========', error)
          this.$toast.fail(error)
        })
    },
    // 获取分包列表
    // getFBList() {
    //   this.$store
    //     .dispatch('gyswdxm/getGysXmfbListAction', { 'xmid': this.xmid, 'zzid': this.userInfo.zzid })
    //     .then(res => {
    //       this.FbList = res
    //       for (let index = 0; index < this.FbList.length; index++) {
    //         if (this.FbList[index].tbzt !== 2 && index === 0) {
    //           this.FbList[index].tbzt = 0
    //         } else if (this.FbList[index].tbzt !== 2 && index !== 0) {
    //           this.FbList[index].tbzt = 1
    //         }
    //       }

    //     })
    //     .catch((error) => {
    //       this.$toast.fail(error)
    //     })
    // },
    // 获取分包内容
    getFBNRList(fbid) {
      this.$store
        .dispatch('gyswdxm/getGysBjListBylcAction', { 'xmid': this.xmid, 'zzid': this.userInfo.zzid, 'fbid': fbid, 'dqlcid': this.dqlcid })
        .then(res => {
          this.FBNRList = res

          console.info('res=======', res)
        })
        .catch((error) => {
          this.pageStyles = 3
          this.$toast.fail(error)
          // this.loading = false
        })
    },
    // 弹框确认
    onConfirm(pick) {
      this.showDMXZPOP = false
      this.dqlcid = this.findKey(this.dmxzArray, pick)
      this.getFBNRList(this.$route.params.fbid)
      this.XzlcShow = false

      // this.FBNRList[this.positionArray[0]].data[this.positionArray[1]][this.positionArray[2]].showvalue = pick
      // this.FBNRList[this.positionArray[0]].data[this.positionArray[1]][this.positionArray[2]].value = this.findKey(this.dmxzArray, pick)
      // // 数据为空
      // this.dmxzArray = {}
      // this.realArray = []
      // this.positionArray = []
    },
    // 弹框取消
    onCancel() {
      this.showDMXZPOP = false
      this.XzlcShow = false
      // 数据为空
      // this.dmxzArray = {}
      // this.realArray = []
      // this.positionArray = []
    }
  }
}
</script>

<style lang="css" scoped>
.nav-bar{box-shadow: none;}
.common-main:last-child{margin-bottom: 0;}
</style>
